import React, { Component } from 'react';
import { HashRouter, Route } from 'react-router-dom';
import './App.css';
import './css/main.css';
import { Navigation } from './components/Navigation';
import { Home } from './components/Home';
import { About } from './components/About';
import { Experience } from './components/Experience';
import { TimeTracker } from './components/TimeTracker';
import { Midnite } from './components/Midnite';
import { Jackpot } from './components/Jackpot';
import { Contact } from './components/Contact';

class App extends Component {
  render() {
    return (
      <HashRouter>
        <div className="App">
          <Navigation />
          <Route path="portfolio/" component={Home} exact />
          <Route path="portfolio/about" component={About} exact />
          <Route path="portfolio/experience" component={Experience} exact />
          <Route path="portfolio/experience/timetracker" component={TimeTracker} exact />
          <Route path="portfolio/experience/midnite" component={Midnite} exact />
          <Route path="portfolio/experience/jackpot" component={Jackpot} exact />
          <Route path="portfolio/contact" component={Contact} exact />

          <footer id="main-footer">
            Copyright &copy; 2018
          </footer>
        </div>
      </HashRouter>
    );
  }
}

export default App;
